/* Tag image */
.chose-image {
  width:55px;
  max-height:55px;
  border-radius: 50%;
  padding: 2px 5px 3px 0px;
  vertical-align:middle;
}

.chose-image.rtl {
  padding: 2px 0px 3px 5px;
}

/* Image for Single mode */
.chose-image-small {
  width: 30px;
  height: 30px;
  max-height: 30px;
  object-fit: cover;
  border-radius: 50%;
  vertical-align: middle;
  margin: 0px 13px 0 0;
}

.chose-image-small.rtl {
  margin: -3px 0 0 3px;
}

/* Images appended to the li(s) */
.chose-image-list {
  width: 30px;
  height: 30px;
  max-height: 30px;
  object-fit: cover;
  border-radius: 50%;
  vertical-align: middle;
  margin: -3px 13px 0 0;
}

.chose-image-list.rtl {
  margin: -3px 0 0 3px;
}

/* Grey-out image for Multi mode */
.chosen-container-multi .result-selected img {
  opacity: 0.3;
}
