/* @group Base */

.chosen-container {
    width: 100% !important;
    position: relative;
    display: inline-block;
    vertical-align: middle;
    font-size: 13px;
    background: #fff;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

    * {
        box-sizing: border-box;
    }

    .chosen-drop {
        position: absolute;
        top: 100%;
        z-index: 1011;
        width: 100%;
        border: 1px solid #aaa;
        border-top: 0;
        background: #fff;
        box-shadow: 0 4px 5px rgba(0, 0, 0, 0.15);
        clip: rect(0, 0, 0, 0);

        &.dropup {
            border-bottom: 0;
            border-top: 1px solid #aaa;
            top: auto;
            bottom: 40px;
            border-top-left-radius: 5px;
            border-top-right-radius: 5px;
        }
    }

    &.chosen-with-drop {
        .chosen-single {
            &.dropup {
                background-image: none;
            }
        }

        .chosen-drop {
            clip: auto;

            &.dropup {
                box-shadow: none;
                // margin-bottom: -16px;
            }
        }
    }

    a {
        cursor: pointer;
    }

    .search-choice .group-name,
    .chosen-single .group-name {
        margin-right: 4px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        font-weight: normal;
        color: #999999;
    }

    .search-choice .group-name:after,
    .chosen-single .group-name:after {
        content: ":";
        padding-left: 2px;
        vertical-align: top;
    }
}

/* @end */
/* @group Single Chosen */

.chosen-container-single {
    .chosen-single {
        position: relative;
        display: block;
        overflow: hidden;
        padding: 8px 15px;
        /* height: 25px; */
        border: 1px solid #EAEBED;
        border-radius: 5px;
        /*background-color: #fff;
  background: linear-gradient(#fff 20%, #f6f6f6 50%, #eee 52%, #f4f4f4 100%);
  background-clip: padding-box;
  box-shadow: 0 0 3px #fff inset, 0 1px 1px rgba(0, 0, 0, 0.1); */
        color: #444;
        text-decoration: none;
        white-space: nowrap;
        line-height: 24px;
    }

    .chosen-default {
        color: #000;
    }

    .chosen-single span {
        display: block;
        overflow: hidden;
        margin-right: 26px;
        text-overflow: ellipsis;
        white-space: nowrap;
    }

    .chosen-single-with-deselect span {
        margin-right: 38px;
    }

    .chosen-single abbr {
        position: absolute;
        top: 6px;
        right: 26px;
        display: block;
        width: 12px;
        height: 20px;
        background: url("chosen-sprite.png") -42px 9px no-repeat;
        font-size: 1px;

        &:hover {
            background-position: -42px -10px;
        }
    }

    &.chosen-disabled .chosen-single abbr:hover {
        background-position: -42px 9px;
    }

    .chosen-single div {
        position: absolute;
        top: 0;
        right: 0;
        display: block;
        width: 18px;
        height: 100%;

        b {
            display: block;
            width: 100%;
            height: 100%;
            background: url("chosen-sprite.png") no-repeat 0px 12px;
        }
    }

    .chosen-search {
        position: relative;
        z-index: 1010;
        margin: 0;
        padding: 3px 4px;
        white-space: nowrap;

        input[type="text"] {
            margin: 1px 0;
            padding: 4px 20px 4px 5px;
            width: 100%;
            height: auto;
            outline: 0;
            border: 1px solid #aaa;
            background: url("chosen-sprite.png") no-repeat 100% -20px;
            font-size: 1em;
            font-family: sans-serif;
            line-height: normal;
            border-radius: 0;
        }
    }

    .chosen-drop {
        margin-top: -1px;
        border-radius: 0 0 4px 4px;
        background-clip: padding-box;
    }

    &.chosen-container-single-nosearch .chosen-search {
        position: absolute;
        clip: rect(0, 0, 0, 0);
    }
}

/* @end */
/* @group Results */

.chosen-container .chosen-results {
    color: #444;
    position: relative;
    overflow-x: hidden;
    overflow-y: auto;
    margin: 0 4px 4px 0;
    padding: 0 0 0 4px;
    max-height: 240px;
    -webkit-overflow-scrolling: touch;

    li {
        display: none;
        margin: 0;
        padding: 5px 11px;
        list-style: none;
        line-height: 15px;
        word-wrap: break-word;
        -webkit-touch-callout: none;

        &.active-result {
            display: list-item;
            cursor: pointer;
            padding: 10px;
        }

        &.disabled-result {
            display: list-item;
            color: #ccc;
            cursor: default;
        }

        &.highlighted {
            background-color: #448AFF;
            /* background-image: linear-gradient(#3875d7 20%, #2a62bc 90%); */
            color: #fff;
        }

        &.no-results {
            color: #777;
            display: list-item;
            background: #f4f4f4;
        }

        &.group-result {
            display: list-item;
            font-weight: bold;
            cursor: default;
        }

        &.group-option {
            padding-left: 15px;
        }

        em {
            font-style: normal;
            text-decoration: underline;
        }
    }
}

/* @end */
/* @group Multi Chosen */

.chosen-container-multi {
    .chosen-choices {
        position: relative;
        overflow: hidden;
        margin: 0;
        padding: 8px 15px;
        border: 1px solid #EAEBED;
        border-radius: 5px;
        width: 100%;
        height: auto;
        background-color: #fff;
        /* background-image: linear-gradient(#eee 1%, #fff 15%); */
        cursor: text;

        li {
            float: left;
            list-style: none;

            &.search-field {
                margin: 0;
                padding: 0;
                white-space: nowrap;

                input[type="text"] {
                    margin: 1px 0;
                    padding: 0;
                    height: 25px;
                    outline: 0;
                    border: 0 !important;
                    background: transparent !important;
                    box-shadow: none;
                    color: #999;
                    font-size: 100%;
                    font-family: sans-serif;
                    line-height: normal;
                    border-radius: 0;
                    width: 25px;
                }
            }

            &.search-choice {
                position: relative;
                margin: 0 5px 0 0;
                padding: 7px 40px 7px 10px;
                border: 1px solid #9A9FA9;
                max-width: 100%;
                border-radius: 25px;
                background-color: #9A9FA9;
                /* background-image: linear-gradient(#f4f4f4 20%, #eeeeee 50%, #e8e8e8 52%, #eee 100%); */
                background-size: 100% 19px;
                background-repeat: repeat-x;
                background-clip: padding-box;
                box-shadow: 0 0 2px #fff inset, 0 1px 0 rgba(0, 0, 0, 0.05);
                color: #fff;
                line-height: 13px;
                cursor: default;

                span {
                    word-wrap: break-word;
                    color: #fff;
                    font-weight: 600;
                }

                .search-choice-close {
                    position: absolute;
                    top: 7px;
                    right: 5px;
                    display: block;
                    width: 12px;
                    height: 12px;
                    background: url("chosen-sprite.png") -42px 2px no-repeat;
                    font-size: 1px;

                    &:hover {
                        background-position: -42px -9px;
                    }
                }
            }

            &.search-choice-disabled {
                padding-right: 5px;
                border: 1px solid #ccc;
                background-color: #e8e8e8;
                background-image: linear-gradient(#f4f4f4 20%, #eeeeee 50%, #e8e8e8 52%, #eee 100%);
                color: #666;
            }

            &.search-choice-focus {
                background: #d4d4d4;

                .search-choice-close {
                    background-position: -42px -10px;
                }
            }
        }
    }

    .chosen-results {
        margin: 0;
        padding: 0;
    }

    .chosen-drop .result-selected {
        display: list-item;
        color: #ccc;
        cursor: default;
    }
}

/* @end */
/* @group Active  */

.chosen-container-active {
    .chosen-single {
        border: 1px solid #5897fb;
        box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
    }

    &.chosen-with-drop .chosen-single {
        border: 1px solid #aaa;
        border-bottom-right-radius: 0;
        border-bottom-left-radius: 0;
        /* background-image: linear-gradient(#eee 20%, #fff 80%); */
        box-shadow: 0 1px 0 #fff inset;

        div {
            border-left: none;
            background: transparent;

            b {
                background-position: -16px 11px;
            }
        }
    }

    .chosen-choices {
        border: 1px solid #5897fb;
        box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);

        li.search-field input[type="text"] {
            color: #000 !important;
        }
    }
}

/* @end */
/* @group Disabled Support */

.chosen-disabled {
    opacity: 0.5 !important;
    cursor: default;

    .chosen-single,
    .chosen-choices .search-choice .search-choice-close {
        cursor: default;
    }
}

/* @end */
/* @group Right to Left */

.chosen-rtl {
    text-align: right;

    .chosen-single {
        overflow: visible;
        padding: 0 8px 0 0;

        span {
            margin-right: 0;
            margin-left: 26px;
            direction: rtl;
        }
    }

    .chosen-single-with-deselect span {
        margin-left: 38px;
    }

    .chosen-single {
        div {
            right: auto;
            left: 3px;
        }

        abbr {
            right: auto;
            left: 26px;
        }
    }

    .chosen-choices li {
        float: right;

        &.search-field input[type="text"] {
            direction: rtl;
        }

        &.search-choice {
            margin: 3px 5px 3px 0;
            padding: 3px 5px 3px 19px;

            .search-choice-close {
                right: auto;
                left: 4px;
            }
        }
    }

    &.chosen-container-single .chosen-results {
        margin: 0 0 4px 4px;
        padding: 0 4px 0 0;
    }

    .chosen-results li.group-option {
        padding-right: 15px;
        padding-left: 0;
    }

    &.chosen-container-active.chosen-with-drop .chosen-single div {
        border-right: none;
    }

    .chosen-search input[type="text"] {
        padding: 4px 5px 4px 20px;
        background: url("chosen-sprite.png") no-repeat -30px -20px;
        direction: rtl;
    }

    &.chosen-container-single {
        .chosen-single div b {
            background-position: 6px 2px;
        }

        &.chosen-with-drop .chosen-single div b {
            background-position: -12px 2px;
        }
    }
}

/* @end */
/* @group Retina compatibility */
@media only screen and (-webkit-min-device-pixel-ratio: 1.5),
only screen and (min-resolution: 144dpi),
only screen and (min-resolution: 1.5dppx) {
    .chosen-rtl .chosen-search input[type="text"] {
        background-image: url("chosen-sprite@2x.png") !important;
        background-size: 52px 37px !important;
        background-repeat: no-repeat !important;
    }

    .chosen-container-single {
        .chosen-single {

            abbr,
            div b {
                background-image: url("chosen-sprite@2x.png") !important;
                background-size: 52px 37px !important;
                background-repeat: no-repeat !important;
            }
        }

        .chosen-search input[type="text"] {
            background-image: url("chosen-sprite@2x.png") !important;
            background-size: 52px 37px !important;
            background-repeat: no-repeat !important;
        }
    }

    .chosen-container-multi .chosen-choices .search-choice .search-choice-close {
        background-image: url("chosen-sprite@2x.png") !important;
        background-size: 52px 37px !important;
        background-repeat: no-repeat !important;
    }

    .chosen-container {

        .chosen-results-scroll-down span,
        .chosen-results-scroll-up span {
            background-image: url("chosen-sprite@2x.png") !important;
            background-size: 52px 37px !important;
            background-repeat: no-repeat !important;
        }
    }
}

/* @end */
